@import "variables";
@import "mixins";
* {
  margin: 0;
  padding: 0;
}
html{
  scroll-behavior: smooth;
}
body {
  font-family: $mainFont;
  background: url("../img/papyrus.png");
}

header {
  background-image: url("../img/bg-header.jpg");
  background-position: center;
  background-size: cover;
  height: 100vh;
  color: white;
  padding: 4rem;
  text-align: center;
}
.header__text {
  position: absolute;
  top: 5rem;
  right: 2rem;
  padding: 1rem 2rem;
  text-shadow: 2px 2px 3px black;
  h1 {
    margin-top: 0;
  }
  h2 {
    margin: 12px 0;
    font-size: 1.8rem;
  }
  .header__icon {
    transition: 0.5s;
    &:hover,
    &:active,
    &:focus {
      color: $sectionBlue;
    }
  }
}
.header__button {
  position: absolute;
  bottom: 5%;
  left: 50%;
  transform: translate(-5%, -50%);
  font-size: 2rem;
  a {
    color: white;
    text-decoration: none;
    text-shadow: 2px 2px 3px black;
  }
  .landing_icon {
    color: white;
    text-shadow: 2px 2px 3px black;
    position: relative;
    font-size: 18px;
    margin-right: 4px;
    position: relative;
    transition: all 0.7s;
  }
}
.header__excla {
  transition: 0.3s;
}

.nav__title{
  @include center;
  flex-direction: column;
  padding: .9rem 0;
  background: $menuBg;
  color: white;
  text-shadow: 0 0 5px black;
  width: 100%;
  h3{
    margin: 0;
    font-size: 1.5rem;
    letter-spacing: 0.1px;
  }
}
.nav__search{
  @include center;
  flex-direction: column;
  justify-content: space-between;
  background: $menuBg;
  color:white;
  font-size:1.2rem;
  text-shadow: 0 0 2px black;
    label{
      padding-top: .5rem;
    }
     input{
      display: inline-block;
      height: 25px;
      font-size: 0.9rem;
      transition: .1s;
      border-radius: 8px;
      border: none;
      box-shadow: 0 0 2px black;
      margin: 1rem 0;
        &:hover{
          box-shadow: 0 0 5px black;
          border: none;
        }
      }
  }
.nav__p--search{
  margin: 0;
  margin-bottom: .3rem;
}

.nav a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 25px;
  color: white;
  display: flex;
  justify-content: center;
  transition: 0.3s;
    &:hover {
      color: #f1f1f1;
    }
  }
  .nav .nav__closeBtn {
    font-size: 36px;
    position: absolute;
    right: 15px;
    top: 5px;
    cursor:pointer;
    background: $menuBg;
  }

.nav__openBtn {
  font-size: 30px;
  width: 40px;
  cursor: pointer;
  background-color: black;
  color: white;
  padding: 10px 10px;
  border: none;
  position: fixed;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  z-index: 20;
  opacity: 1;
  transition: 0s;
  &:hover {
    background-color: #444;
  }
}
.red {
  color: $redLetter;
}
.blue {
  color: $blueLetter;
}
.yellow {
  color: $yellowLetter;
}

section,
article {
  height: 300px;
  padding: 2rem;
  margin: 0 0 2rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  &.with__dividers {
    background: $sectionBlue;
    margin-bottom: 0;
    position: relative;
    .songdata {
      background: #ffaf3d;
    }
    .section__header {
      text-shadow: 2px 2px 3px black;
      color: white;
      margin-top: 0;
    }
  }
}

svg {
  display: block;
}
#curveUpColor path,
#curveDownColor path,
#curveUpColorResult path {
  fill: $sectionBlue;
  stroke: $sectionBlue;
  box-sizing: border-box;
}

.section__header {
  font-family: $headerFont;
  font-size: 2rem;
  text-shadow: 2px 2px 3px gray;
  letter-spacing: 0.35px;
  font-weight: 300;
  margin-bottom: 3rem;
}

@import 'rwd';